import React from 'react';
import firebase from './firebase';
import ShowGoals from './ShowGoals';

class ChooseTask extends React.Component {
    constructor(props) {
        super(props)
        this.state = {
            goals: [],
            showForm: false,
            currentPhys: 0,
            currentMent: 0,
            currentEnergy: 0,
            bestOption: '',
        }
    }

    componentDidMount() {
        const dbRef = firebase.database().ref(this.props.un)
        
        dbRef.on('value', (response) => {
            const newState = [];
            const data = response.val();
            let arr = Object.values(data)
            arr.forEach((obj) => {
                newState.push({
                    name: obj.name,
                    physical: obj.physical,
                    mental: obj.mental,
                    energy: obj.energy,
                })
            })
            this.setState({
                goals: newState
            })
        })
    }

    showForm = (event) => {
        event.preventDefault();
        this.setState ({
            showForm: true
        })
    }
    
    submit = (event) => {
        event.preventDefault();
        this.setState ({
            showForm: false
        })
        let winner = ''

        //math.abs gives absolute value so we can track difference regardless of more or less energy currently
        this.state.goals.forEach((obj) => {
            let x = 0
            let currentBestVal = 1000
            let currentBestName = ''
            x = x + Math.abs(obj.physical - this.state.currentPhys) 
            x = x + Math.abs(obj.mental - this.state.currentMent)
            x = x + Math.abs(obj.energy - this.state.currentEnergy)
            if (x < currentBestVal){
                currentBestVal = x
                currentBestName = obj.name
            }
            winner = currentBestName
        })
        this.setState ({
            bestOption: winner
        })
    }

    handleChangePhys = (event) => {
        this.setState({
            currentPhys: event.target.value
        })
    }

    handleChangeMent = (event) => {
        this.setState({
            currentMent: event.target.value
        })
    }

    handleChangeEnergy = (event) => {
        this.setState({
            currentEnergy: event.target.value
        })
    }

    complete = (event) => {
        const dbRef = firebase.database().ref(this.props.un)
        dbRef.child(this.state.bestOption).remove();
        this.setState ({
            bestOption: ''
        })
        this.props.checkTaskCount()
    }

    render(){
        if (this.state.showForm){
            return (
                <form className="centered">
                    <h2>How Are you Feeling Now?</h2>

                    <div className="inputPair">
                        <label htmlFor="physical">How physically energized are you?</label>
                        <input type="range" min="0" max="10" name="physical" value={this.state.physical} onChange={this.handleChangePhys}/>
                    </div>

                    <div className="inputPair">
                        <label htmlFor="mental">How mentally energized are you?</label>
                        <input type="range" min="0" max="10" name="mental" value={this.state.mental} onChange={this.handleChangeMent}/>
                    </div>

                    <div className="inputPair">
                        <label htmlFor="energy">How much energy do you have?</label>
                        <input type="range" min="0" max="10" name="energy" value={this.state.energy} onChange={this.handleChangeEnergy}/>
                    </div>
                    <button onClick={this.submit}>Submit</button>
                </form>
            )
        }
        if (this.state.bestOption !== ''){
            return (
                <div className="centered">
                    <p>The most suitable task for you currently is:</p>
                    <h2>{this.state.bestOption}</h2>
                    <button onClick={this.complete}>Mark Task as Complete</button>
                </div>
            )
        }
        return (
            <div className="centered">
                <button onClick={this.showForm}>Choose a task for me!</button>
                <ShowGoals un={this.props.un} />
            </div>

        )
    }
}

export default ChooseTask